import {
  IonHeader,
  IonIcon,
  IonItem,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import { addOutline, logOutOutline } from "ionicons/icons";
import React from 'react';
import { Link } from "react-router-dom";
import "./Menu.css";

const Menu: React.FC= () => {
  return (
    <>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Menu</IonTitle>
        </IonToolbar>
      </IonHeader>
      <div className="menu">
        <Link to={"/addProduct"}>
          <IonItem>
            <IonIcon icon={addOutline} slot="start" />
            <h1>Add Products</h1>
          </IonItem>
        </Link>
        <IonItem>
          <IonIcon icon={logOutOutline} slot="start" />
          <h1> LogOut</h1>
        </IonItem>
      </div>
    </>
  );
}
export default Menu;
