import React, { useState } from "react";
import { IonButton, IonContent, IonLoading, IonPage } from "@ionic/react";
import { Link, useHistory } from "react-router-dom";
import { useIonViewWillEnter } from "@ionic/react";
import "./Home.css";

const Home: React.FC = () => {
  const [showLoading, setShowLoading] = useState(false);
  const history = useHistory();

  useIonViewWillEnter(() => {
    setShowLoading(true)
    const data = localStorage.getItem("sellerData");
    if (data) {
      history.push("/sellerDashboard");
      setShowLoading(false);
    console.log(data);
    }else{
      setShowLoading(false);
    }
  });

  return (
    <IonPage>
      <IonContent fullscreen>
        <IonLoading
          cssClass="my-custom-class"
          isOpen={showLoading}
          onDidDismiss={() => setShowLoading(false)}
          message={"Please wait..."}
        />
        <div className="box">
          <div className="heading">
            <h1>Welcome</h1>
            <p>Continue as a </p>
          </div>
          <div className="action-buttons ion-padding">
            <Link to={"/userLogin"}>
              <IonButton size="large" className=" login-button">
                Customer
              </IonButton>
            </Link>
            <p>Or</p>
            <Link to={"/sellerLogin"}>
              <IonButton size="large" className=" login-button">
                Seller
              </IonButton>
            </Link>
          </div>
        </div>
      </IonContent>
    </IonPage>
  );
};

export default Home;
