import React, { useState } from "react";
import {
  IonPage,
  IonContent,
  IonInput,
  IonButton,
  IonLabel,
  IonItem,
  IonIcon,
  IonAlert,
  IonLoading,
} from "@ionic/react";
import Axios from "axios";
import {
  eyeOffOutline,
  eyeOutline,
  lockClosed,
  mailOutline,
} from "ionicons/icons";
import "./Login.css";
import { Link, useHistory } from "react-router-dom";

const UserLogin: React.FC = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const [alertMessage, setAlertMessage] = useState("");
  const [showLoading, setShowLoading] = useState(false);
  const history = useHistory();

  const toggleShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleLogin = () => {
    try {
      setShowLoading(true);
      Axios.post("http://localhost:4000/userLogin", {
        email,
        password,
      })
        .then((response: any) => {
          setShowLoading(false);
          console.log(response.data);
          history.push("/dashboard");
        })
        .catch((error: any) => {
          console.error(error);
          setAlertMessage("Invalid email or password");
          setShowAlert(true);
          setShowLoading(false);
        });
    } catch (error) {
      console.error(error);
      setAlertMessage("Invalid email or password");
      setShowAlert(true);
      setShowLoading(false);
    }
  };

  return (
    <IonPage>
      <IonContent>
        <IonLoading
          cssClass="my-custom-class"
          isOpen={showLoading}
          onDidDismiss={() => setShowLoading(false)}
          message={"Please wait..."}
        />
        <div className="login-section ion-padding">
          <div className="heading">
            <h1>Welcome</h1>
            <p>Sign in to continue</p>
          </div>
          <div className="login-form ion-padding">
            <div className="form-input">
              <IonItem>
                <IonIcon icon={mailOutline} slot="start" />
                <IonLabel position="floating">Email</IonLabel>
                <IonInput
                  type="email"
                  value={email}
                  onIonChange={(e) => setEmail(e.detail.value!)}
                />
              </IonItem>
            </div>
            <div className="form-input">
              <IonItem>
                <IonIcon icon={lockClosed} slot="start" />
                <IonLabel position="floating">Password</IonLabel>
                <IonInput
                  type={showPassword ? "text" : "password"}
                  value={password}
                  onIonChange={(e) => setPassword(e.detail.value!)}
                />
                <IonIcon
                  icon={showPassword ? eyeOutline : eyeOffOutline}
                  slot="end"
                  onClick={toggleShowPassword}
                  className="showpassword-button"
                />
              </IonItem>
            </div>
          </div>
          <IonAlert
            isOpen={showAlert}
            onDidDismiss={() => setShowAlert(false)}
            message={alertMessage}
            buttons={["OK"]}
          />
          <div className="action-buttons ion-padding">
            <IonButton
              size="large"
              onClick={handleLogin}
              className="login-button"
            >
              Login
            </IonButton>
            <p>Forgot password ?</p>
            <Link to={"/userSignup"}>
              <IonButton size="large" fill="outline" className="singup-button">
                Signup
              </IonButton>
            </Link>
          </div>
        </div>
      </IonContent>
    </IonPage>
  );
};

export default UserLogin;
