import React, { useEffect, useState } from "react";
import {
  IonPage,
  IonContent,
  IonButtons,
  IonHeader,
  IonMenuButton,
  IonTitle,
  IonToolbar,
  IonMenu,
  IonSelect,
  IonSelectOption,
  IonButton,
  IonCol,
  IonGrid,
  IonRow,
  IonLabel,
  IonAlert,
  IonImg,
  IonItem,
  IonList,
} from "@ionic/react";
import "./SellerDashboard.css";
import Menu from "../components/menu";
import { useIonViewWillEnter } from "@ionic/react";
import Axios from "axios";

// interface ImageData {
//   _id: string;
//   image: {
//     contentType: string;
//     data: {
//       type: string;
//       data: number[];
//     };
//   };
//   __v: number;
// }

const SellerDashboard: React.FC = () => {
  const [selectedOption1, setSelectedOption1] = useState("11Am To 9Pm");
  const [selectedOption2, setSelectedOption2] = useState("Open");
  const [searchQuery, setSearchQuery] = useState("");
  const [shopName, setShopName] = useState(null);
  const [apartments, setApartments] = useState(null);
  const [showAlert, setShowAlert] = useState(false);
  const [data, setData] = useState([]);
  const url = "http://localhost:4000/shop";
  const chunkSize = 50;

  const handleOption1Change = (event: CustomEvent) => {
    setSelectedOption1(event.detail.value);
    console.log(event.detail.value);
  };

  const handleOption2Change = (event: CustomEvent) => {
    setSelectedOption2(event.detail.value);
  };

  const handleSubmit = (e: any) => {
    e.preventDefault();
    // setShowLoading(true);
    Axios.post(url, {
      apartments: apartments,
      shopName: shopName,
      timing: selectedOption1,
      status: selectedOption2,
    })
      .then((res: any) => {
        // setShowLoading(false);
        setShowAlert(true);
        console.log(res);
      })
      .catch((err: any) => {
        // setShowLoading(false);
      });
  };

  useEffect(() => {
    handleSearch();
  }, [searchQuery]);

  useIonViewWillEnter(() => {
    const data = localStorage.getItem("sellerData");
    if (data) {
      const item = JSON.parse(data);
      setSearchQuery(item.email);
      console.log(item.email);
    }
  });

  const handleSearch = () => {
    Axios.get(`http://localhost:4000/seller/${searchQuery}`).then(
      (response: any) => {
        console.log(response);
        setShopName(response.data.shopName);
        setApartments(response.data.apartments);
      }
    );
  };

  useEffect(() => {
    Axios.get("http://localhost:4000/product")
      .then((response: any) => {
        console.log(response.data);
        setData(response.data);
      })
      .catch((err: any) => {
        console.log(err);
      });
  }, []);

  return (
    <>
      <IonMenu contentId="main-content">
        <Menu />
      </IonMenu>
      <IonPage id="main-content">
        <IonHeader>
          <IonToolbar>
            <IonButtons slot="start">
              <IonMenuButton></IonMenuButton>
            </IonButtons>
            <IonTitle className="ion-text-center">{shopName}</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <IonAlert
            isOpen={showAlert}
            onDidDismiss={() => setShowAlert(false)}
            header="Alert"
            message="Status updated successfully"
            buttons={["OK"]}
          />
          <div className="headerForm">
            <IonGrid>
              <IonRow>
                <IonCol size="6">
                  <IonLabel>Shop Timing</IonLabel>
                  <IonSelect
                    value={selectedOption1}
                    onIonChange={handleOption1Change}
                  >
                    <IonSelectOption value="11Am To 9Pm">
                      11Am To 9Pm
                    </IonSelectOption>
                    <IonSelectOption value=" 8Am To 9Pm2">
                      8Am To 9Pm
                    </IonSelectOption>
                    <IonSelectOption value=" 7Am To 9Pm">
                      7Am To 9Pm
                    </IonSelectOption>
                  </IonSelect>
                </IonCol>
                <IonCol size="6">
                  <IonLabel>Today's Status</IonLabel>
                  <IonSelect
                    value={selectedOption2}
                    onIonChange={handleOption2Change}
                  >
                    <IonSelectOption value="Open">Open</IonSelectOption>
                    <IonSelectOption value="Close">Close</IonSelectOption>
                  </IonSelect>
                </IonCol>
              </IonRow>
              <IonRow>
                <IonCol>
                  <IonButton expand="block" onClick={handleSubmit}>
                    Submit
                  </IonButton>
                </IonCol>
              </IonRow>
            </IonGrid>
          </div>
          <div className="product">
            <h1>Products</h1>
            <IonList>
              {data.map((product: any, index) => {
                const base64String = btoa(
                  String.fromCharCode(
                    ...new Uint8Array(product.image.data.data)
                  )
                );
                return (
                  <IonItem key={index}>
                    <IonImg
                      src={`data:${product.image.contentType};base64,${base64String}`}
                      slot="start"
                    />
                    <IonLabel>
                      <h2>{product.productName}</h2>
                      <p>{product.price}</p>
                      <p>{product.description.slice(0, 20)}...</p>
                    </IonLabel>
                  </IonItem>
                );
              })}
            </IonList>
          </div>
        </IonContent>
      </IonPage>
    </>
  );
};

export default SellerDashboard;
