import React, { useState } from "react";
import {
  IonPage,
  IonContent,
  IonInput,
  IonButton,
  IonLabel,
  IonItem,
  IonIcon,
  IonLoading,
  IonSelect,
  IonSelectOption,
} from "@ionic/react";
import Axios from "axios";
import {
  lockClosed,
  mailOutline,
  personOutline,
  eyeOutline,
  eyeOffOutline,
  businessOutline,
} from "ionicons/icons";
import "./Signup.css";
import { Link, useHistory } from "react-router-dom";

const UserSignup: React.FC = () => {
  const [apartments, setApartments] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [showLoading, setShowLoading] = useState(false);
  const history = useHistory();
  const apartment = [
    "Emerald Apartments",
    "Imperial Apartments",
    "Sunderayan Apartments",
    "Mahalaxmi apartment",
    "Block 45 Apartments",
    "M P Ratawal's Apartment",
    "Friends Apartment",
    "GULMOHAR APARTMENTS",
    "GOENKA APARTMENTS",
    "Essel Apartments",
  ];

  const toggleShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const url = "http://localhost:4000/userSingup";

  const handleSignup = (e: any) => {
    e.preventDefault();
    setShowLoading(true);
    Axios.post(url, {
      apartments:apartments,
      name: name,
      email: email,
      password: password,
    })
      .then((res: any) => {
        console.log(res);
        history.push("/dashboard");
        setShowLoading(false);
      })
      .catch((err: any) => {
        setShowLoading(false);
      });
  };

  return (
    <IonPage>
      <IonContent>
        <IonLoading
          cssClass="my-custom-class"
          isOpen={showLoading}
          onDidDismiss={() => setShowLoading(false)}
          message={"Please wait..."}
        />
        <div className="signup-section ion-padding">
          <div className="heading">
            <h1>Create an account!</h1>
            <p>Start shopping with us</p>
          </div>
          <div className="login-form ion-padding">
            <div className="form-input">
              <IonItem>
                <IonIcon icon={businessOutline} slot="start" />
                <IonLabel position="floating">Apartment</IonLabel>
                <IonSelect
                  value={apartments}
                  onIonChange={(e) => setApartments(e.detail.value!)}
                >
                  <IonSelectOption>Choose a apartment</IonSelectOption>
                  {apartment.map((item) => (
                    <IonSelectOption key={item} value={item}>
                      {item}
                    </IonSelectOption>
                  ))}
                </IonSelect>
              </IonItem>
            </div>
            <div className="form-input">
              <IonItem>
                <IonIcon icon={personOutline} slot="start" />
                <IonLabel position="floating">Name</IonLabel>
                <IonInput
                  type="text"
                  value={name}
                  onIonChange={(e) => setName(e.detail.value!)}
                />
              </IonItem>
            </div>
            <div className="form-input">
              <IonItem>
                <IonIcon icon={mailOutline} slot="start" />
                <IonLabel position="floating">Email</IonLabel>
                <IonInput
                  type="email"
                  value={email}
                  onIonChange={(e) => setEmail(e.detail.value!)}
                />
              </IonItem>
            </div>
            <div className="form-input">
              <IonItem>
                <IonIcon icon={lockClosed} slot="start" />
                <IonLabel position="floating">Password</IonLabel>
                <IonInput
                  type={showPassword ? "text" : "password"}
                  value={password}
                  onIonChange={(e) => setPassword(e.detail.value!)}
                />
                <IonIcon
                  icon={showPassword ? eyeOutline : eyeOffOutline}
                  slot="end"
                  onClick={toggleShowPassword}
                  className="showpassword-button"
                />
              </IonItem>
            </div>
          </div>
          <div className="action-buttons ion-padding">
            <IonButton
              size="large"
              onClick={handleSignup}
              className="login-button"
            >
              Sign up
            </IonButton>
            <p>Forgot password ?</p>
            <Link to={"/userLogin"}>
              <IonButton size="large" fill="outline" className="singup-button">
                Login
              </IonButton>
            </Link>
          </div>
        </div>
      </IonContent>
    </IonPage>
  );
};

export default UserSignup;
